.detail{
	padding: 24px 36px;
	box-sizing: border-box;
	background: #fff;
	/* box-shadow: 0 0px 10px 0 rgb(0 64 128 / 10%); */
	margin: 0 10px;

	blockquote{
		position: relative;
		/* border-left: 4px solid #cbcbcb; */
		padding: 10px 20px;
		margin: 0;
		background: #f8f8f8;
		border-radius: 4px;
		position: relative;
	}
	
	blockquote::before{
		content: '';
		width: 4px;
		height: 100%;
		position: absolute;
		left: 0px;
		top: 50%;
		transform: translateY(-50%);
		border-radius: 2px;
		background: #cbcbcb;
		
	}
	
	p > code{
		background-color: #fff5f5;
		color: #ff502c;
		padding: 3px 5px;
		border-radius: 3px;
	}
	
	pre{
		border-radius: 8px;
		box-shadow: 0 0  10px 0px rgba(0,0,0,.5);
	}

}

