@import "../../theme.scss";

.box{
	width: 100%;
	height: 100%;

	.list{
		padding: 20px 20px 20px 0px;
		height: 100%;
		box-sizing: border-box;
		overflow: scroll;

		&::-webkit-scrollbar {/*滚动条整体样式*/
			width: 5px;     /*高宽分别对应横竖滚动条的尺寸*/
			height: 1px;
		}

		&::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
			border-radius: 10px;
			background: rgba(0,0,0,.3);
		}

		.article-box{
			margin-bottom: 50px;
		}

		.title{
			font-size: 20px;
			font-weight: bold;
		}
		
		.title:hover{
			cursor: pointer;
		}
	
		.article-box > p:nth-child(2){
			display: flex;
			align-items: center;
			margin-top: 12px;
		}
	
		.create-time,
		.tag{
			color: #bfbfbf;
			font-size: 14px;
		}
	
		.icon{
			display: inline-block;
			width: 15px;
			height: 15px;
			background: url('../../assets/tag.png') no-repeat;
			background-size: contain;
			margin-left: 20px;
			margin-right: 5px;
		}
	
		.tag{
			margin-right: 12px;
		}

	}

	.user-info{
		padding-top: 120px;
		width: 360px;
		height: 100%;
		float: right;
		display: flex;
		flex-direction: column;
		align-items: center;
		color: $primary;
		box-sizing: border-box;


		.avatar{
			width: 120px;
			height: 120px;
			border-radius: 100%;
		}

		.name{
			font-size: 18px;
			margin-top: 20px;
		}

		.desc{
		}
	}
}