@import "../../theme.scss";

.nav-bar{
	width: 100%;
	height: 60px;
	overflow: hidden;
	padding: 0 120px;
	box-sizing: border-box;
	
	.info{
		height: 100%;
		float: left;
		display: flex;
		align-items: center;

		.avatar{
			height:40px;
			width:40px;
			border:1px solid red;
			border-radius: 100%;
		}

		.name{
			font-size: 16px;
			margin-left: 12px;
		}
	}

	.menus{
		float: right;

		.nav{
			display: inline-block;
			height: 60px;
			line-height: 60px;
			padding: 0 20px;
			margin-right: 16px;
			text-decoration: none;
			color: $textColor;

			&:last-child{
				margin-right: -20px;
			}

			&:hover{
				background: #eee;
			}

			&.active{
				color: #000;
				position: relative;
				&::before{
					content: '';
					position: absolute;
					width: 60%;
					left: 20%;
					top: 0;
					height: 5px;
					border-bottom-left-radius: 5px;
					border-bottom-right-radius: 5px;
					background: $primary;
				}
			}
		}
	}
}

