.clock{
	border: 1px solid red;
	display: flex;

	&>div{
		width: 50%;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding-top: 60px;

		.button{
			width: 100px;
			height: 100px;
			border-radius: 100%;
			box-shadow: 0px 1px 10px rgb(245, 238, 238);
			line-height: 100px;
			text-align: center;
			margin: 30px 0;
			transition: all .5s ease;

			&:hover{
				box-shadow:0px 1px 10px rgb(204, 201, 201);
				cursor: pointer;
			}
		}

		.title{
			margin-bottom: 24px;
		}
	}
}